@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "metropolis";
  /* src: url("https://yoursite.com/css/fonts/CustomFont.eot"); */
  /* src: url("https://yoursite.com/css/fonts/CustomFont.woff") format("woff"), */
  src: url("/fonts/Metropolis-Regular.otf") format("opentype")
  /* url("https://yoursite.com/css/fonts/CustomFont.svg#filename") format("svg"); */
  }

  * {
    font-family: "metropolis";
    color: #656565;
  }

  .clip {
    position: absolute;
    z-index: -100;
    top: 0;
    max-width: 100%;
    height: 170vh;
    width: 100vw;
    background-color: #118DCD;
    -webkit-clip-path: polygon(100% 0, 100% 24%, 0 100%, 0 55%, 68% 0);
    clip-path: polygon(100% 0, 100% 24%, 30% 100%, 0 100%, 90% 0);
  }

  .clip2 {
    position: absolute;
    z-index: -1;
    top: 0;
    max-width: 100%;
    height: 170vh;
    width: 100vw;
    background-color: #15B3FF;
    -webkit-clip-path: polygon(10% 0, 50% 0, 100% 50%, 100% 90%);
    clip-path: polygon(10% 0, 45% 0, 100% 45%, 100% 80%);
  }

  main {
    padding: 0 100px 0 100px;
  }

.main-button {
  background-color: #118DCD;
  border: none;
  padding: 10px;
  color:#FFF;
  font-family: "metropolis";
  font-size: 16px;
  border-radius: 30px;
}

.main-button:hover {
  cursor: pointer;
}

footer {
  background-color: #FFF;
  width: 100%;
  text-align: center;
  line-height: 100px;
  height: 100px;
  margin-top: 100px;
  font-weight: bold;
  box-shadow:         3px 3px 5px 06px #ccc;
}




























